@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');

* {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  height: 100%;
  vertical-align: middle;
  font-size: 0;
}

a {
  text-decoration: none;
}

.app-wrap {
  display: grid;
  grid-template-rows: 80px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas: 
  "header header"
  "aside content";
  max-width: 1200px;
  margin: 0 auto;
}

.content {
  grid-area: content;
}

.content {
  padding-bottom: 30px;
}

ul {
  list-style: none;
}

button {
  background: #0d6baa;
  height: 47px;
  padding: 0 22px;
  margin: 0;
  color: #fff;
  border: none;
  border-radius: 6px;
  width: max-content;
  cursor: pointer;
  transition: all .3s;
}

button:disabled,
button:disabled:hover {
  background-color: rgb(94, 91, 94);
  cursor: not-allowed;
}

button:hover {
  background: #052d47;
}