.aside {
  grid-area: aside;
  background-color: thistle;
}

nav ul {
  padding: 10px 20px;
}

nav li a {
  font-weight: 500;
  font-size: 18px;
  color: #2b2626;
  transition: all .3s;
}

nav li a:hover {
  text-decoration: underline;
}

nav li a.active {
  text-decoration: underline;
  font-weight: 600;
}